<template>
  <div class="loading center" v-if="loading">
    <spinner spinnerColor="magenta"></spinner>
  </div>
  <div class="error" v-else-if="error">Error XD</div>
  <div class="shirtList" v-else>
    <div
      class="shirt"
      v-for="shirt in shirts"
      :key="shirt.id"
      @click="openShirt(shirt.id)"
    >
      <shirt :color="shirt.color" class="shirtView"></shirt>
      <shirt-listing-info
        :size="shirt.size"
        :color="shirt.color"
        :likes="shirt.likes"
        :timestamp="shirt.timestamp"
      ></shirt-listing-info>
    </div>
  </div>
</template>

<style scoped>
.shirtList {
  width: 100%;
  display: grid;
  grid-template-columns: 1fr;
  gap: 1rem;
  color: white;
}
@media (min-width: 460px) {
  .shirtList {
    grid-template-columns: 1fr 1fr;
  }
}
@media (min-width: 800px) {
  .shirtList {
    grid-template-columns: 1fr 1fr 1fr;
  }
}
@media (min-width: 1150px) {
  .shirtList {
    grid-template-columns: 1fr 1fr 1fr 1fr;
  }
}

.shirt {
  display: grid;
  place-items: center;
  grid-template-columns: 1fr;
  grid-template-rows: 4fr 3fr;
  background: rgba(0, 0, 0, 0.4);
  padding: 1rem;
}
.shirt:hover {
  filter: brightness(80%);
  cursor: pointer;
}
</style>

<script>
import axios from "axios";
import Shirt from "@/components/Shirt.vue";
import ShirtListingInfo from "@/components/ShirtListingInfo.vue";
import Spinner from "@/components/Spinner.vue";

export default {
  name: "ShirtListing",
  props: {
    page: {
      type: Number,
      default: 0,
    },
    sort: {
      type: String,
      default: "popular",
    },
    sortDirection: {
      type: String,
      default: "desc",
    },
  },
  watch: {
    page() {
      this.fetchPage();
    },
    sort() {
      this.fetchPage();
    },
    sortDirection() {
      this.fetchPage();
    },
  },
  data() {
    return {
      shirts: [],
      loading: false,
      error: false,
    };
  },
  mounted() {
    console.log("PAge:", this.page);
    this.fetchPage();
  },
  methods: {
    async fetchPage() {
      try {
        this.loading = true;
        this.error = false;

        const endpoint = "https://shirt-graphql-server.herokuapp.com/graph";
        const query = `
          query getShirtListing($page: Int, $sort: Sort, $sortDirection: SortDirection) {
            listing: shirtListing(page: $page, sort: $sort, sortDirection: $sortDirection) {
              shirts {
                color,
                size,
                creator,
                likes,
                id,
                timestamp,
              },
              page
            }
          }
        `;
        const body = {
          query,
          variables: {
            page: this.page,
            sort: this.sort,
            sortDirection: this.sortDirection,
          },
        };

        const response = (await axios.post(endpoint, body)).data;
        console.log("Response:", response);

        const { shirts } = response.data.listing;
        console.log("Fetched Shirts:", shirts);

        this.shirts = shirts;
        this.loading = false;
      } catch (err) {
        console.log("Error @fetchShirtPage");
        console.log("Error:", err);
        this.error = true;
        this.loading = false;
      }
    },
    openShirt(id) {
      this.$router.push({ name: "colored", params: { id } });
    },
  },
  components: {
    Spinner,
    Shirt,
    ShirtListingInfo,
  },
};
</script>
