<template>
  <main-header></main-header>
  <div class="viewer">
    <div class="loading center" v-if="loading">
      <spinner :spinnerColor="spinnerColor"></spinner>
    </div>
    <div class="error center" v-else-if="error">
      <h2>Shirt not found</h2>
    </div>
    <div class="shirtUI" v-else>
      <div class="shirtViewer center">
        <gradient-shirt
          :colorStops="shirt.color_stops"
          :direction="shirt.direction"
          :type="shirt.type"
        ></gradient-shirt>
      </div>
      <gradient-shirt-info
        :size="shirt.size"
        :colorStops="shirt.color_stops"
        :type="shirt.type"
        :likes="shirt.likes"
        :timestamp="shirt.timestamp"
        :creator="shirt.creator"
        :id="shirt.id"
      ></gradient-shirt-info>
    </div>
  </div>
</template>

<style scoped>
.viewer {
  padding: 2rem 0;
  height: 60%;
}
.loading {
  display: flex;
  align-items: center;
  justify-content: center;
  height: 100%;
}

.shirtUI {
  display: grid;
  grid-template-columns: 1fr;
  grid-template-rows: 60% 40%;
  place-items: center;
  background: rgba(0, 0, 0, 0.4);
  margin: 0 auto;
  padding: 1rem;
  height: 60vh;
  width: clamp(200px, 60vw, 1200px);
}
@media (min-width: 1000px) {
  .shirtUI {
    grid-template-columns: 60% 40%;
    grid-template-rows: 1fr;
  }
}
.shirtViewer {
  width: 100%;
  height: 100%;
}
</style>

<script>
import axios from "axios";
import MainHeader from "@/components/Header.vue";
import Spinner from "@/components/Spinner.vue";
import GradientShirt from "@/components/GradientShirt.vue";
import GradientShirtInfo from "@/components/GradientShirtInfo.vue";

export default {
  name: "shirtView",
  props: {
    id: {
      default: false,
    },
  },
  data() {
    return {
      spinnerColor: "magenta",
      shirt: {},
      loading: false,
      error: false,
    };
  },
  watch: {
    async id() {
      if (!this.id) {
        await this.regenerateId();
        return;
      }
      this.searchShirt();
    },
  },
  computed: {
    shirtColor() {
      return (this.shirt.color || "").toUpperCase();
    },
  },
  async mounted() {
    if (!this.id) {
      await this.regenerateId();
      return;
    }
    this.searchShirt(this.id);
  },
  methods: {
    async getShirtCount() {
      const endpoint = "https://shirt-graphql-server.herokuapp.com/graph";
      const query = `
        query getShirtCount {
          count: shirtCount
        }
      `;
      const body = {
        query,
      };
      const response = (await axios.post(endpoint, body)).data;
      console.log("Response:", response);
      const { count } = response.data;
      return count;
    },
    async searchShirt(id = this.id || 1) {
      if (this.loading) return;
      this.loading = true;
      this.error = false;
      try {
        const endpoint = "https://shirt-graphql-server.herokuapp.com/graph";
        const query = `
          query getGradientShirt ($id: ID!) {
            shirt: gradientShirt(id: $id) {
              id,
              color_stops,
              direction,
              type,
              size,
              creator,
              likes,
              timestamp,
            },
          }
        `;
        const body = {
          query,
          variables: {
            id,
          },
        };
        const response = (await axios.post(endpoint, body)).data;
        const { shirt } = response.data;

        console.log("Shirt:", shirt);
        if (!shirt) throw new Error("No Shirt Found");
        this.shirt = shirt;
        this.loading = false;
      } catch (err) {
        console.log("Error @searchShirt");
        console.log("Error:", err);
        this.loading = false;
        this.error = true;
      }
    },
    async randomShirtId() {
      const shirtCount = await this.getShirtCount();
      const randomId = Math.round(Math.random() * shirtCount);
      return randomId;
    },
    async regenerateId() {
      if (this.loading) return;
      this.loading = true;
      const randomId = await this.randomShirtId();
      const route = {
        name: "colored",
        params: {
          id: randomId,
        },
      };
      this.$router.push(route);
      this.loading = false;
      this.regenerating = false;
    },
  },
  components: {
    MainHeader,
    Spinner,
    GradientShirt,
    GradientShirtInfo,
  },
};
</script>
