<template>
  <div class="shirtInfo">
    <div class="size">
      <div class="icon sizeIcon">
        <img src="../assets/size.svg" alt="Size" />
      </div>
      <span class="sizeAbb"> {{ size }} </span>
    </div>
    <div class="color">
      <div class="icon colorIcon">
        <img src="../assets/color.svg" alt="Color" />
      </div>
      <span class="colorHex"> {{ color.toUpperCase() }} </span>
    </div>
    <div class="likes">
      <div class="icon likeIcon">
        <img src="../assets/like.svg" alt="Likes" />
      </div>
      <span class="likeCount"> {{ likes }} </span>
    </div>
    <div class="date">
      <div class="icon dateIcon">
        <img src="../assets/date.svg" alt="Date" />
      </div>
      <span class="timestamp"> {{ dateify(timestamp) }} </span>
    </div>
  </div>
</template>

<style scoped>
.shirtInfo {
  width: 100%;
}
.size,
.color,
.likes,
.date {
  width: 100%;
  display: flex;
  justify-content: flex-start;
  align-items: center;
}
.icon {
  fill: white;
  margin-right: 5%;
}
</style>

<script>
export default {
  name: "Shirt Listing Info",
  props: {
    size: {
      type: String,
      default: "",
    },
    color: {
      type: String,
      default: "",
    },
    likes: {
      type: Number,
      default: 0,
    },
    timestamp: {
      type: String,
      default: "",
    },
  },
  methods: {
    dateify(timestamp) {
      const ms = Number.parseInt(timestamp);
      const date = new Date(ms);
      return `${date.getFullYear()}-${date.getMonth() + 1}-${date.getDate()}`;
    },
  },
};
</script>
