<template>
  <main-header></main-header>
  <div class="listing">
    <div class="top">
      <div class="listingSelector">
        <button @click="listing = 'color'">Colored</button>
        <button @click="listing = 'gradient'">Gradient</button>
      </div>
      <div class="sorter">
        <select id="sortDirection" v-model="sortDirection">
          <option value="desc">Desc</option>
          <option value="asc">Asc</option>
        </select>
        <select id="sortSelect" v-model="sort">
          <option value="popular">Popular</option>
          <option value="latest">Latest</option>
        </select>
      </div>
    </div>
    <shirt-listing
      v-if="listing === 'color'"
      :page="pageNumber"
      :sort="sort"
      :sort-direction="sortDirection"
    ></shirt-listing>
    <gradient-shirt-listing
      v-else-if="listing === 'gradient'"
      :page="pageNumber"
      :sort="sort"
      :sort-direction="sortDirection"
    ></gradient-shirt-listing>
    <div class="pageNavigator">
      <div class="button prevButton" @click="previousPage">Back</div>
      <div class="currentPage">{{ pageNumber }}</div>
      <div class="button nextButton" @click="nextPage">Next</div>
    </div>
  </div>
</template>

<style scoped>
.listing {
  padding: 2rem 0;
  width: 80%;
  max-width: 1200px;
  margin: 0 auto;
}

.top {
  width: 100%;
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin: 1rem 0;
}
.listingSelector,
.sorter {
  width: 50%;
  display: flex;
  align-items: center;
}
.listingSelector {
  justify-content: flex-start;
}
.sorter {
  justify-content: flex-end;
}
.sorter select {
  margin: 0 1rem;
  background: hsl(0, 0%, 25%);
  border: none;
  color: hsl(0, 0%, 95%);
  padding: 0.3rem;
}

.pageNavigator {
  width: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  margin: 2rem auto;
}
.currentPage {
  background: rgba(0, 0, 0, 0.4);
  padding: 0.5rem 1rem;
  color: white;
  margin: 0 1rem;
}
.pageNavigator .button {
  background: hsl(300, 6%, 20%);
  color: hsl(0, 0%, 85%);
  padding: 0.4rem 0.5rem;
}
.pageNavigator .button:hover {
  background: hsl(300, 6%, 10%);
  cursor: pointer;
}
</style>

<script>
import mainHeader from "@/components/Header.vue";
import ShirtListing from "@/components/ShirtListing.vue";
import GradientShirtListing from "@/components/GradientShirtListing.vue";

export default {
  name: "Listing",
  props: {
    page: {
      default: 0,
    },
  },
  data() {
    return {
      listing: "color",
      sort: "popular",
      sortDirection: "desc",
      loading: false,
      error: false,
    };
  },
  computed: {
    pageNumber() {
      return Number.parseInt(this.page || "0");
    },
  },
  methods: {
    previousPage() {
      if (Number.parseInt(this.pageNumber) <= 0) return;
      this.$router.push(`/listing/${this.pageNumber - 1}`);
    },
    nextPage() {
      this.$router.push(`/listing/${this.pageNumber + 1}`);
    },
  },
  components: {
    mainHeader,
    ShirtListing,
    GradientShirtListing,
  },
};
</script>
