<template>
  <div class="shirtSvg" :style="{ fill: `url(#gradient-${uuid})` }">
    <svg viewBox="0 0 213.12 172.52">
      <defs>
        <linearGradient
          v-if="type === 'linear'"
          :id="gradientId"
          :gradientTransform="`rotate(${direction} 0.5 0.5)`"
        >
          <stop
            v-for="(color, index) in colorStops"
            :key="index"
            :offset="gradientOffset(colorStops.length, index)"
            :stop-color="color"
          />
        </linearGradient>
        <radialGradient
          v-else-if="type === 'radial'"
          :id="gradientId"
          cx="50%"
          cy="50%"
          r="50%"
          fx="50%"
          fy="50%"
          :gradientTransform="`rotate(${direction} 0.5 0.5)`"
        >
          <stop
            v-for="(color, index) in colorStops"
            :key="index"
            :offset="gradientOffset(colorStops.length, index)"
            :stop-color="color"
          />
        </radialGradient>
      </defs>
      <g transform="translate(-463.15 -207.81)">
        <path
          d="m520.32 210.04c-0.83 0.03-1.68 0.25-2.46 0.7l-49.87
          28.8c-2.49 1.44-3.34 4.6-1.9 7.09l16.99 29.43c1.44 2.49 4.61
          3.34 7.1 1.9l26.06-15.05v109.98c0 2.88 2.32 5.2 5.2 5.2h96.29c2.88
          0 5.19-2.32 5.19-5.2v-110.13l26.34 15.2c2.49 1.44 5.65 0.59
          7.09-1.9l16.99-29.43c1.44-2.49
          0.59-5.65-1.9-7.09l-49.87-28.8c-1.01-0.58-2.14-0.79-3.21-0.66-0.21-0.02-0.42-0.04-0.63-0.04h-27.58c-0.41
          3.28-9.41 5.9-20.44 5.9s-20.02-2.62-20.43-5.9h-27.84c-0.17 0-0.33 0.02-0.5 0.03-0.2-0.02-0.41-0.03-0.62-0.03z"
        />
      </g>
    </svg>
  </div>
</template>

<style scoped>
.shirtSvg {
  max-width: 100%;
  max-height: 100%;
}
.shirtSvg svg {
  max-width: 70%;
  max-height: 70%;
}
</style>

<script>
import uniqueIdMixin from "@/mixins/uniqueId.js";

export default {
  name: "Shirt",
  mixins: [uniqueIdMixin],
  props: {
    colorStops: {
      type: Array,
      default: () => [""],
    },
    direction: {
      type: String,
      default: "",
    },
    type: {
      type: String,
      default: "",
    },
  },
  computed: {
    gradientId() {
      return `gradient-${this.uuid}`;
    },
  },
  methods: {
    gradientOffset(stopLength, index) {
      const length = stopLength - 1 <= 0 ? 1 : stopLength - 1;
      const offset = Math.floor((index / length) * 100).toString() + "%";
      return offset;
    },
  },
};
</script>
