<template>
  <div class="shirtInfo">
    <div class="field size">
      <div class="icon sizeIcon">
        <img src="../assets/size.svg" alt="Size" />
      </div>
      <span class="sizeAbb"> {{ size }} </span>
    </div>
    <div class="field type">
      <div class="icon typeIcon">
        <img src="../assets/gradient.svg" alt="Gradient Type" />
      </div>
      <span class="gradientType"> {{ capitalize(type) }} </span>
    </div>
    <div class="field colors" @click="toggleColors">
      <img src="../assets/color.svg" alt="Color" class="icon colorIcon" />
      <div class="colorList" :style="colorStyles">
        <span class="color" v-for="color in colorStops" :key="color">
          {{ color }}
        </span>
      </div>
      <div :class="['expandableIcon', showColors ? 'flipped' : '']">
        <img src="../assets/expand.svg" alt="open/close" />
      </div>
      <!--
        Basically the plan is, show a "Preview" of the gradient
        and make the color field expandable, when expanded it
        shows a list of the colors that make up the gradient,
        and a button to copy each color
      -->
    </div>
    <div class="field date">
      <div class="icon dateIcon">
        <img src="../assets/date.svg" alt="Date" />
      </div>
      <span class="timestamp"> {{ dateify(timestamp) }} </span>
    </div>
    <!-- 
      Im not adding likes for now because i cant regulate who likes how many times
      and i dont want spamming to the heroku server, so it will only be a part of the Listing for now
    <div class="field likes">
      <div class="icon likeIcon">
        <img src="../assets/like.svg" alt="Likes" />
      </div>
      <span class="likeCount"> {{ likes }} </span>
    </div>
    -->
    <div class="field creator">
      <img class="icon" src="../assets/user.svg" alt="creator" />
      <span> {{ creator }}</span>
    </div>
    <div class="field id">
      <span class="icon">ID</span>
      <span> {{ id }} </span>
    </div>
  </div>
</template>

<style scoped>
.shirtInfo {
  width: 100%;
  display: flex;
  flex-direction: column;
  padding-right: 1rem;
}
.shirtInfo .field {
  display: flex;
  justify-content: flex-start;
  align-items: center;
  flex-basis: 2.5rem;
  color: white;
  background: hsl(0, 0%, 25%);
  margin: 0.5rem 0;
  padding: 0.3rem;
  border-radius: 5px;
}
.shirtInfo .icon {
  box-sizing: content-box;
  padding-right: 0.5rem;
  margin-right: 0.5rem;
  text-align: center;
  width: 24px;
  height: 24px;
  border-right: 1px solid white;
}
.icon {
  fill: white;
  margin-right: 5%;
}
.expandableIcon {
  transform: rotate(0deg);
  transition: transform 0.5s ease;
  margin-left: auto;
  margin-right: 0.5rem;
}
.colorList {
  transition: max-height 0.5s ease;
  overflow: hidden;
}
.color {
  display: block;
}
.flipped {
  transform: rotate(180deg);
}
</style>

<script>
export default {
  name: "ShirtInfo",
  props: {
    colorStops: {
      type: Array,
      default: () => [],
    },
    type: {
      type: String,
      default: "",
    },
    size: {
      type: String,
      default: "",
    },
    likes: {
      type: Number,
      default: 0,
    },
    timestamp: {
      type: String,
      default: "",
    },
    creator: {
      type: String,
      default: "",
    },
    id: {
      type: String,
      default: "",
    },
  },
  data() {
    return {
      showColors: false,
    };
  },
  computed: {
    colorStyles() {
      const el = document.getElementsByClassName("colorList")[0];
      const styles = {
        maxHeight: this.showColors ? el.scrollHeight + "px" : "0px",
      };
      return styles;
    },
  },
  methods: {
    toggleColors() {
      this.showColors = !this.showColors;
    },
    dateify(timestamp) {
      const ms = Number.parseInt(timestamp);
      const date = new Date(ms);
      return `${date.getFullYear()}-${date.getMonth() + 1}-${date.getDate()}`;
    },
    capitalize(string) {
      if (!string) return string;
      const pieces = string.split("");
      return pieces.shift().toUpperCase() + pieces.join("");
    },
  },
};
</script>
