<template>
  <div class="shirtInfo">
    <div class="field color">
      <img src="../assets/color.svg" alt="Color" class="icon colorIcon" />
      <span class="color"> {{ (color || "").toUpperCase() }} </span>
    </div>
    <div class="field size">
      <img src="../assets/size.svg" alt="Size" class="icon sizeIcon" />
      <span class="size"> {{ size }} </span>
    </div>
    <div class="field timestamp">
      <img
        src="../assets/date.svg"
        alt="Timestamp"
        class="icon timestampIcon"
      />
      <span class="creator"> {{ dateify(timestamp) }} </span>
    </div>
    <div class="field creator">
      <img src="../assets/user.svg" alt="Creator" class="icon creatorIcon" />
      <span class="creator"> {{ creator }} </span>
    </div>
    <div class="field id">
      <span class="icon idIcon center">ID</span>
      <span class="identifier"> {{ id }} </span>
    </div>
  </div>
</template>

<style scoped>
.shirtInfo {
  width: 100%;
  display: flex;
  flex-direction: column;
  padding-right: 1rem;
}
.shirtInfo .field {
  color: white;
  margin: 0.5rem 0;
  background: hsl(0, 0%, 25%);
  border-radius: 5px;
  padding: 0.3rem;
  display: flex;
  justify-content: flex-start;
  align-items: center;
}
.shirtInfo .icon {
  box-sizing: content-box;
  padding-right: 0.5rem;
  margin-right: 0.5rem;
  text-align: center;
  width: 24px;
  height: 24px;
  border-right: 1px solid white;
}
</style>

<script>
export default {
  name: "Shirt Info",
  props: {
    color: {
      type: String,
      default: "",
    },
    size: {
      type: String,
      default: "",
    },
    creator: {
      type: String,
      default: "",
    },
    timestamp: {
      type: String,
      default: "",
    },
    id: {
      type: String,
      default: "",
    },
  },
  methods: {
    dateify(timestamp) {
      const ms = Number.parseInt(timestamp);
      const date = new Date(ms);
      return `${date.getFullYear()}-${date.getMonth() + 1}-${date.getDate()}`;
    },
  },
};
</script>
